.offcanvas {
  &__menu {
    padding-top: 0.8rem;

    li {
      position: relative;
      font-weight: 700;
      margin-bottom: 1.5rem;
      font-size: 20px;
      text-transform: uppercase;

      &::after {
        content: '';
        background: url('../img/svg/chevron-right.svg') no-repeat;
        width: 18px;
        height: 18px;
        display: block;
        position: absolute;
        right: 0;
        top: 6px;
        z-index: -1;
      }

    }

    a {
      z-index: 2;
      flex: 1;
      display: flex;
      transition: all 0.3s ease-in-out 0s;
      width: 100%;
      color: $white;

      &:hover {
        color: $pink;
      }
    }
  }

  &__contacts {
    padding-top: 1.5rem;
    border-top: 2px solid $pink;

    .phone {
      margin-bottom: 1rem;
    }

    .telegram {
      svg {
        margin-right: 1rem;
      }
    }

    a {
      flex: 1;
      display: flex;
      font-size: 20px;
      font-weight: 500;
      color: $white;
    }
  }


  &__social {
    padding-top: 2rem;

    p {
      font-size: 20px;
      font-weight: 500;
      color: $white;
    }

    ul {
      display: flex;
    }

    li {
      &:first-child {
        margin-right: 1rem;
      }
    }

    svg {
      color: $pink;
    }
  }


  &__button {
    border: 0;
    height: 100%;
    width: 60px;
    position: absolute;
    right: 0;
    background-color: transparent;
    transition: all 0.3s ease-in-out 0s;

    svg {
      color: $white;
    }
  }


}

.offcanvas {
  background-color: #0c142c;
}

.offcanvas-header {
  position: relative;
  background-color: $pink;
  color: $white;
}

.btn-close {
  &::before {
    content: '';
    background-color: #fff;
  }
}
