.slider-brand {
  position: relative;

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 0;
    width: 150px;
    height: 100%;
    z-index: 5;
  }

  &::before {
    left: 0;
    background: linear-gradient(to right, #f7faff 10%, rgba(237, 237, 237, 0) 90%)
  }

  &::after {
    right: 0;
    background: linear-gradient(to right, rgba(237, 237, 237, 0) 0%, #f7faff 90%)
  }


  .swiper-wrapper {
    -webkit-transition-timing-function: linear !important;
    transition-timing-function: linear !important;
  }


  &__block {
    padding: 10rem 0 4rem;
    background: radial-gradient(87.94% 298.21% at 87.94% 35.3%, #F7FAFF 12.1%, #F7FAFF 78.25%);
  }

  &__item {
    padding: 1rem;
    border-radius: 15px;
    background-color: $white;
    display: flex;
    height: 70px;
    align-items: center;
    justify-content: center;

  }

  &__item-img img {
    max-width: 150px;
    width: 100%;
    height: 70px;
    object-fit: contain;
  }

  .swiper-slide {
    width: 200px;
  }

}
