.modal {
  &__content-img {
    border-radius: 15px;
    overflow: hidden;
    position: relative;
  }

  &__content-title {
    display: inline-block;
    border-radius: 5px;
    font-size: 40px;
    font-weight: 700;
    margin: 0 0 2rem;
  }

  &__content-text {
    font-weight: 400;
  }

  &__content-wrap {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }


  &__content-img a::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
    z-index: 1;
    opacity: 0;
    transition: all 500ms ease;


  }

  &__content-img {
    &:hover a::before {
      opacity: 1;
    }
  }


  .main__link {
      font-size: 20px;
      font-weight: 500;
  }


}



.modal-content {
  border-radius: 15px;
}

.modal-header {
  border-bottom: 0;
}

.modal-body {
  padding: 4rem;
}


.btn-close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 1;

  &:focus {
    box-shadow: none;
  }
}
