.map {
    position: relative;
    height: 600px;


    &__content {
        display: flex;
        border-radius: 15px;
        max-width: 800px;
        width: 100%;
        overflow: hidden;
        z-index: 2;
    } 

    &__container {
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        display: flex;
        align-items: center;
    }

    &__content-contacts {
        color: $white;
        background-color: $dark-blue;
        padding: 2rem;
        width: 100%;

        .title {
            margin-bottom: 2rem;
        }
    }

    &__info {
        background-color: $red;
        color: $white;
        padding: 2rem;
        width: 100%;

        .title {
            margin-bottom: 2rem;
        }

        ul {
            margin: 0;
        }

        li {
            font-weight: 400;
            margin-bottom: 0.3rem;
        }

    }

    &__content-contacts-row {
        margin-bottom: 1rem;

        &:hover a {
            color: $pink;
        }

        &:hover svg {
            color: $pink;
        }

        a {
            color: $white;
            padding-left: 0.45rem;
            transition: all 0.3s ease-in-out 0s;
            font-weight: 400;

        }

        svg {
            position: relative;
            top: -1px;
            transition: all 0.3s ease-in-out 0s;
        }
    }

    &__desc {
        display: flex;
        font-weight: 500;
        font-size: 14px;
    }
}

#map {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    z-index: 1;
    position: relative;
}
  