.tariff {

  &__control-slider {
    display: flex;
    padding-bottom: 2rem;
  }


  &__section {
    margin-top: 3rem;
    padding-bottom: 5rem;
    position: relative;
    z-index: 1;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 50%;
      background-color: $pink;
      z-index: -1;
    }


    .arrow-left {
      transform: rotate(180deg);
    }

    .arrow-right {
      margin-left: 2rem;
    }

    .arrow-right,
    .arrow-left {
      width: 56px;
      height: 26px;
      display: flex;
      align-items: center;
      transition: color .3s, transform .2s;

      &:hover {
        color: $pink;
      }
    }

    .arrow-right:active {
      transform: translateX(-0.5rem);
    }

    .arrow-left:active {
      transform: rotate(180deg) translateX(-0.5rem);
    }

    .arrow-left.swiper-button-disabled,
    .arrow-right.swiper-button-disabled {
      opacity: .35;
    }

    .swiper-slide {
      display: flex;
    }

    .card-footer {
      background-color: transparent;
      text-align: center;
      border: 0;
      padding-bottom: 2rem;
    }

    .card {
      border: 1px solid rgba(196, 196, 196, 0.3);
      transition: all ease-in-out 0.3s;
      border-radius: 15px;
    }

    .card-body {
      padding: 2rem 3rem;
    }

    .btn-main {
      color: $dark-gray;

      &:hover {
        color: $white;
      }
    }
  }

  &__title {
    color: rgb(52, 55, 69);
    text-align: center;
    font-size: 35px;
    font-weight: 700;
    margin-bottom: 2rem;
    transition: all 0.3s;
    transition-timing-function: cubic-bezier(0.7, 0, 0.9, 1);
  }

  &__card {


    &:hover .tariff__title {
      color: $pink;

    }
  }

  &__price-wrapper {
    text-align: center;
    padding-bottom: 1rem;
  }

  &__price-current,
  &__currency {
    color: $pink;
    font-weight: 700;
    font-size: 30px;
  }

  &__services-item {
    position: relative;
    padding-left: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: 400;

    &::before {
      content: "";
      background: url('../img/svg/check.svg');
      position: absolute;
      left: 0;
      top: 2px;
      width: 20px;
      height: 20px;
    }
  }
}
