.header {

  &__contacts {
    display: flex;
    align-items: center;
  }

  .nav-link {
    font-size: 15px;
    font-weight: 500;
    color: $dark-gray;

    &::before {
      content: "";
      position: absolute;
      width: 0;
      height: 2px;
      bottom: 3px;
      left: 10px;
      background-color: $pink;
      visibility: hidden;
      width: 0px;
      transition: all 0.3s ease-in-out 0s;
    }

    &:hover.nav-link::before {
      visibility: visible;
      width: 25px;
    }
  }

  &__telegram {
    display: flex;
    align-items: center;

    a {
      color: $dark-gray;
      transition: all 0.3s ease-in-out 0s;

      &:hover {
        color: $pink;
      }
    }
    

    svg {
      margin-right: 0.5rem;
    }

  }

  &__phone {
    margin-right: 3rem;
    font-weight: 500;
    font-size: 15px;
    

    a {
      color: $dark-gray;
      transition: all 0.3s ease-in-out 0s;

      &:hover {
        color: $pink;
      }
    }



  }

  &__menu {
    .nav-item {
      margin-right: 3rem;

      &:last-child {
        margin-right: 0;
      }


    }

    .nav-link {
      transition: all 0.3s ease-in-out 0s;
      position: relative;

      &:hover {
        color: $pink;
      }

    }

  }

}



.navbar-brand {
  margin-right: 3.75rem;

  img {
    max-width: 180px;
    width: 100%;
    height: 50px;
    object-fit: contain;
  }
}


.headroom {
  will-change: transform;
  background-color: #fff;
  box-shadow: 0 6px 11px -6px rgba(0, 0, 0, .1);
}

.headroom--pinned {
  transform: translateY(0);
  background-color: #fff;
  transition: all ease 0.3s;
  box-shadow: 0 6px 11px -6px rgba(0, 0, 0, .1);

}

.headroom--unpinned {
  transform: translateY(-100%);
  background-color: #fff;
  transition: all ease 0.3s;
}

.headroom--top {
  background-color: transparent;
  box-shadow: none;
}

.navbar-toggler {
  height: 45px;
  width: 45px;
  padding: 0.6rem;
  border: 1px solid rgba(136, 131, 190, .3);
  border-radius: 50%;
  transition: all ease 0.3s;

  &:focus {
    box-shadow: none;
    background-color: $pink;
  }

  &:focus svg {
    color: $white;
  }

  &:hover {
    background-color: $pink;
  }

  &:hover svg {
    color: $white;
  }

}
