.portfolio {

  &__card-middle {
    height: 500px;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
  }


  &__waves {
    background-image: url('../img/svg/kit-wave-2.svg');
    background-repeat: no-repeat;
    background-position: right;
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    right: 0;
    top: 15%;
  }

  &__waves-two {
    background-image: url('../img/svg/kit-wave-3.svg');
    background-repeat: no-repeat;
    background-position: bottom left;
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    
  }

  &__card-middle.portfolio__card--part .portfolio__card-img {
    width: 357px;
    height: 350px;
  }

  &__card-large {
    height: 600px;
    position: relative;
    border-radius: 10px;
    overflow: hidden;

    .work__item {
      height: 600px;
      min-height: 600px;
    }

    .work__item-img {
      transform: scale(1);
    }
  }

  &__card-small {
    height: 250px;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
  }

  &__card-large.portfolio__card--logo #logo-svg {
    width: 500px;
    height: 500px;
  }

  &__card-large a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }


  &__company {
    height: 300px;

    p {
      font-weight: 400;
      font-size: 20px;
      color: $dark-gray;
      margin-bottom: 2rem;

    }
  }


  &__card--part-2 {
    padding: 3rem;
    background-color: #F2F2F2;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    &:hover .portfolio__overlay {
      transform: scale(5) translateZ(0);
      
    }

    &:hover .portfolio__card-circle {
      border-color: #fff;
    }

    &:hover .portfolio__card-circle::after {
      background-color: #AFCEFF;
    }

 
    

    
    
  }

  &__card-circle {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background: #fff;
    border: 2px solid #E8E8E8;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    transition: all 0.3s ease-out;

    img {
      width: 130px;
      height: 130px;
      object-fit: contain;
      z-index: 10000;
      transform: translateZ(0);
    }

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      display: block;
      position: absolute;
      background: #fff;
      border-radius: 50%;
      top: 0;
      left: 0;
      transition: all 0.3s ease-out;
    }
  }

  &__overlay {
    width: 120px;
    position: absolute;
    height: 120px;
    border-radius: 50%;
    background: #AFCEFF;
    top: 60px;
    right: 60px;
    z-index: 0;
    transition: transform 0.3s ease-out;
  }

  &__section {
    position: relative;
    margin-top: 4rem;
  }

  &__page {
    position: relative;
    margin-top: 8rem;
    padding-bottom: 5rem;
  }

  &__card-txt {
    position: absolute;
    z-index: 10;
    bottom: 1.2rem;
    left: 2rem;
    display: flex;
    flex-direction: column;
    color: $white;
  }

  &__desc {
    z-index: 99;
    position: absolute;
    top: 2rem;
    left: 2rem;
    color: $white;
    max-width: 250px;
    width: 100%;

    p {
      font-weight: 400;
    }
  }

  &__desc.dark-text {
    color: $dark-gray;
  }

  &__desc-name {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 1.2rem;
  }

  &__card-txt.dark-text {
    color: $dark-gray;
  }

  &__card-txt.dark-text .portfolio__card-ht {
    color: rgba(0, 0, 0, 0.5);
  }

  &__card-name {
    font-weight: 700;
    text-transform: uppercase;
    font-size: 14px;
  }

  &__card-ht {
    color: rgba(255, 255, 255, 0.5);
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 400;
  }

  &__card {
    position: relative;
    height: 400px;
    border-radius: 10px;
    overflow: hidden;
  }

  &__card--part a {
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  &__card--part {
    .portfolio__card-img {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      background: url('../img/portfolio/albagroup/07.png') no-repeat 0 0;
      width: 255px;
      height: 250px;
      z-index: 30;
      pointer-events: none;
      background-size: cover;
    }
  }

  &__card--brand {
    background-color: #f7f7f7;

    .portfolio__card-img {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      background: url('../img/portfolio/ud-group/logo-dark.png') no-repeat 0 0;
      width: 240px;
      height: 40px;
      z-index: 30;
      pointer-events: none;
      background-size: contain;
    }


    .btn {
      position: absolute;
      right: 14px;
      bottom: 14px;
      width: 30px;
      height: 30px;
      background-color: #00335e;
      border: none;
      border-radius: 30px;
      cursor: pointer;
      outline: none;
      transition: all 0.5s ease;
    }

    &:hover .btn {
      transform: scale(60);
    }

    &:hover .portfolio__card-img {
      background: url('../img/portfolio/ud-group/logo-light.png') no-repeat 0 0;
      background-size: contain;
    }

    &:hover .portfolio__card-txt.dark-text {
      color: $white;
    }

    &:hover .portfolio__card-txt.dark-text .portfolio__card-ht {
      color: rgba(255, 255, 255, 0.5);
    }

    &:hover .portfolio__desc.dark-text {
      color: $white;
    }

  }



  &__card--door {
    &:hover .portfolio__bg-in {
      opacity: 1;
      -webkit-transition: 0.8s;
      -o-transition: 0.8s;
      transition: 0.8s;
    }

    .portfolio__card-txt {
      color: #343745;
    }

    .portfolio__card-ht {
      color: rgba(0, 0, 0, 0.5);
    }

  }

  &__card--logo {
    background: #E12A47;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    #logo-svg {
      width: 380px;
      height: 380px;
    }

    a:after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }


  }






  &__bg-wrap {
    overflow: hidden;
    display: block;
    position: relative;
    height: 100%;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background: url(../img/portfolio/01.png) no-repeat;
    background-size: auto;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    opacity: 1;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
  }

  &__bg-in {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    background: url(../img/portfolio/02.png) no-repeat;
    background-size: auto;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
  }
}


$easeOutExpo: cubic-bezier(0.190, 1.000, 0.220, 1.000);


.work {
  &__item {
    position: relative;
    height: 400px;
    min-height: 400px;
    perspective: 300px;

    &:after {
      position: absolute;
      z-index: 0;
      top: 10px;
      right: 10px;
      bottom: 10px;
      left: 10px;
      display: block;
      content: '';
      background: transparent;
      opacity: 0;
      transition: opacity 1s ease-in-out;
    }
  }

  &__item-inner {
    position: relative;
    z-index: 1;
    height: 100%;
    background-color: #dde4ff;
    overflow: hidden;
    // transform-style: preserve-3d;
  }


  &__item-img-outer {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
  }

  &__item-img {
    transform: scale(1.3);
    width: 100%;
    height: 100%;
    object-fit: cover;
  }


}









.slider-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;

}

.before-wrapper {
  display: block;
  overflow: hidden;
  width: auto;
  height: 100%;
  position: relative;
  background: url('../img/portfolio/albagroup/bg07.jpg') no-repeat center center;
  background-size: auto;
  background-size: cover;

}

.after-wrapper {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transform: translateX(50%);
  -ms-transform: translateX(50%);
  transform: translateX(50%);
}

.after-image {
  display: block;
  width: auto;
  height: 100%;
  position: relative;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  background: url('../img/portfolio/albagroup/bg06.jpg') no-repeat center center;
  background-size: auto;
  background-size: cover;
}
