.reviews {
  &__section {
    padding-top: 3rem;
    position: relative;
    z-index: 1;

    .swiper-pagination {
      bottom: 0;
    }

    .main-title {
      text-align: center;
    }

    .swiper-wrapper {
      padding-bottom: 3rem;
    }

    .swiper-pagination-bullet {
      width: 10px;
      height: 10px;
      transition: all 0.3s;
      border-radius: 15px;
    }

    .swiper-pagination-bullet-active {
      width: 30px;
      background: $pink;
    }

    .swiper-button-next,
    .swiper-button-prev {
      color: $white;
      background-color: $pink;
      border-radius: 50%;
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.3s;

      &:hover {
        opacity: 0.8;
      }
    }

    .swiper-button-next::after {
      margin-left: 4px;
    }

    .swiper-button-prev::after {
      margin-right: 4px;
    }

    .swiper-button-next::after,
    .swiper-button-prev::after {
      font-size: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }


  &__circle {
    background-image: url('../img/svg/kit-circle.svg');
    background-repeat: no-repeat;
    background-position: 0px -100px;
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
  }

  &__container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
  }

  &__block {
    padding: 4rem 5rem;
    max-width: 638px;
    width: 100%;
    border: 1px solid rgba(196, 196, 196, 0.3);
    border-radius: 10px;
    background-color: $white;
    margin-right: 2rem;

    p {
      font-size: 18px;
    }
  }

  &__title {
    font-weight: 700;
    margin-bottom: 1.2rem;
  }

  &__img img {
    object-fit: cover;
  }

  &__doc {
    border: 1px solid rgba(196, 196, 196, 0.3);
    border-radius: 10px;
    overflow: hidden;
    max-width: 300px;
    width: 100%;

    a {
      position: relative;
      display: flex;
      padding: 1rem;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
        z-index: 1;
        opacity: 0;
        transition: all 500ms ease;
      }
    }

    &:hover a::before {
      opacity: 1;
    }

    img {
      width: 100%;
      height: 100%;
      max-height: 380px;
      object-fit: contain;
    }
  }

  &__user {
    display: flex;
    align-items: center;
    margin-top: 3rem;
  }

  &__user-wrap {
    margin-left: 1rem;
  }

  &__name {
    font-weight: 700;
  }

  &__post {
    font-size: 14px;
    font-weight: 400;
    opacity: 0.5;
  }
}
