.about {
    position: relative;
    margin: 5rem 0 9rem;


    &__content {
        padding-left: 2rem;

        .title {
            font-size: 60px;
            font-weight: 800;
            color: #343745;
        }

        p {
            font-size: 25px;
        }

       
    }

    &__wave{
        position: absolute;
        top: 0;
        right: 0;
        z-index: -1;
    }

    &__info {
        background-color: $red;
        color: $white;
        padding: 3rem;
        border-radius: 10px;

        .title {
            margin-bottom: 1.25rem;
            line-height: 2.65rem;
        }

        ul {
            margin: 0;
        }

        li {
            font-weight: 400;
            margin-bottom: 0.3rem;
        }

    }

    &__doc {
        padding: 2rem 0;
    
        a {
          transition: all 0.3s ease-in-out 0s;
          font-size: 14px;
          font-weight: 600;
          color: $pink;
    
        }
    
        svg {
          margin-left: 0.3rem;
        }
    

      }
}